/* cabin-regular - latin */
@font-face {
  font-family: 'Cabin';
  font-style: normal;
  font-weight: 400;
  src: url('/fonts/cabin/cabin-v14-latin-regular.eot'); /* IE9 Compat Modes */
  src: local('Cabin'), local('Cabin-Regular'),
       url('/fonts/cabin/cabin-v14-latin-regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('/fonts/cabin/cabin-v14-latin-regular.woff2') format('woff2'), /* Super Modern Browsers */
       url('/fonts/cabin/cabin-v14-latin-regular.woff') format('woff'), /* Modern Browsers */
       url('/fonts/cabin/cabin-v14-latin-regular.ttf') format('truetype'), /* Safari, Android, iOS */
       url('/fonts/cabin/cabin-v14-latin-regular.svg#Cabin') format('svg'); /* Legacy iOS */
}
/* cabin-italic - latin */
@font-face {
  font-family: 'Cabin';
  font-style: italic;
  font-weight: 400;
  src: url('/fonts/cabin/cabin-v14-latin-italic.eot'); /* IE9 Compat Modes */
  src: local('Cabin Italic'), local('Cabin-Italic'),
       url('/fonts/cabin/cabin-v14-latin-italic.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('/fonts/cabin/cabin-v14-latin-italic.woff2') format('woff2'), /* Super Modern Browsers */
       url('/fonts/cabin/cabin-v14-latin-italic.woff') format('woff'), /* Modern Browsers */
       url('/fonts/cabin/cabin-v14-latin-italic.ttf') format('truetype'), /* Safari, Android, iOS */
       url('/fonts/cabin/cabin-v14-latin-italic.svg#Cabin') format('svg'); /* Legacy iOS */
}
