       url('/fonts/ibm_plex_mono/ibm-plex-mono-v5-latin-italic.ttf') format('truetype'), /* Safari, Android, iOS */
       url('/fonts/ibm_plex_mono/ibm-plex-mono-v5-latin-italic.svg#IBMPlexMono') format('svg'); /* Legacy iOS */
