/* zilla-slab-regular - latin */
@font-face {
  font-family: 'Zilla Slab';
  font-style: normal;
  font-weight: 400;
  src: url('/fonts/zilla_slab/zilla-slab-v5-latin-regular.eot'); /* IE9 Compat Modes */
  src: local('Zilla Slab'), local('ZillaSlab-Regular'),
       url('/fonts/zilla_slab/zilla-slab-v5-latin-regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('/fonts/zilla_slab/zilla-slab-v5-latin-regular.woff2') format('woff2'), /* Super Modern Browsers */
       url('/fonts/zilla_slab/zilla-slab-v5-latin-regular.woff') format('woff'), /* Modern Browsers */
       url('/fonts/zilla_slab/zilla-slab-v5-latin-regular.ttf') format('truetype'), /* Safari, Android, iOS */
       url('/fonts/zilla_slab/zilla-slab-v5-latin-regular.svg#ZillaSlab') format('svg'); /* Legacy iOS */
}
/* zilla-slab-italic - latin */
@font-face {
  font-family: 'Zilla Slab';
  font-style: italic;
  font-weight: 400;
  src: url('/fonts/zilla_slab/zilla-slab-v5-latin-italic.eot'); /* IE9 Compat Modes */
  src: local('Zilla Slab Italic'), local('ZillaSlab-Italic'),
       url('/fonts/zilla_slab/zilla-slab-v5-latin-italic.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('/fonts/zilla_slab/zilla-slab-v5-latin-italic.woff2') format('woff2'), /* Super Modern Browsers */
       url('/fonts/zilla_slab/zilla-slab-v5-latin-italic.woff') format('woff'), /* Modern Browsers */
       url('/fonts/zilla_slab/zilla-slab-v5-latin-italic.ttf') format('truetype'), /* Safari, Android, iOS */
       url('/fonts/zilla_slab/zilla-slab-v5-latin-italic.svg#ZillaSlab') format('svg'); /* Legacy iOS */
}
